import React, { Component } from 'react'

export default class Timeline extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="timeline">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <span className="heading-meta">KARIERA</span>
                <h2 className="colorlib-heading animate-box">Doświadczenie</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-3">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Projektant stron internetowych w WEBCAT<span>02.2018-teraz</span></h2>
                        <p>Webcat to mój jednoosobowy projekt, który trwa od stycznia 2018. W tym czasie zrealizowałem ok. 20 nieszablonowych stron internetowych, które biznesom klientów rosnąć. </p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInTop">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-4">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Projektant stron / Administrator stron w KAN sp. z o.o.<span>10.2012 - 01.2018</span></h2>
                        <p>W KAN zajmowałem się tworzeniem, administrowaniem i promocją wszystkich serwisów i aplikacji internetowych firmy obecnej na ponad 20 rynkach na świecie i zatrudniającej ok. 800 osób. W ciągu ponad 5 lat pracy stworzyłem kilkadziesiąt witryn i landing page'y, współpracowałem z agencjami kreatywnymi i graficznymi, biurami tłumaczeń, a także software house'ami gdy była potrzeba stworzenia bardziej zaawansowanych aplikacji. W KAN otrzymałem możliwość uczestniczenia w wielu szkoleniach prowadzonych przez ekspertów w swoich dziedzinach takich jak Maciej Lewiński (google analytics i adwords), Paweł Zakrzewski (Adobe Photoshop, Illustrator, Indesign), Piotr Konieczny (bezpieczeństwo webaplikacji) i szeregu innych. Miałem możliwość przetestowania wielu narzędzi, środowisk, wyrobienia opinii i nabrania doświadczenia, które doprowadziło mnie do chęci spróbowania własnego biznesu. </p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Specjalista ds. Marketingu Internetowego / Administrator stron www <span>03.08.2012 - 08.08.2012</span></h2>
                        <p>W Predica budowaliśmy nową stronę internetową firmy, zbudowaną na Sharepoint. Zajmowałem się także jej promocją. Po 5 moich miesiącach w Predica, oddział w Białymstoku został zamknięty. </p> 
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}