import React, { Component } from 'react'

export default class About extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-about" data-section="about">
        <div className="colorlib-narrow-content">
            <div className="row">
            <div className="col-md-12">
                <div className="row row-bottom-padded-sm animate-box" data-animate-effect="fadeInLeft">
                <div className="col-md-12">
                    <div className="about-desc">
                    <span className="heading-meta">O mnie</span>
                    <h2 className="colorlib-heading">Czym się zajmuję?</h2>
                    <p>Przygodę z aplikacjami webowymi zaczynałem od wyglądu strony ucząc się html, css i photoshop. Do pewnego momentu wtyczki wordpress pozwalały na osiągnięcie wielu rezultatów bez napisania linijki kodu, ale nie na budowę bardziej zaawansowanych i spersonalizowanych aplikacji. Dziś czuję się co raz pewniej w Javascript i React. Dużo czasu poświęcam na naukę typescript, alogrytmów, a także bibliotek przyspieszających pracę. Między zleceniami dla klientów, pracuję nad swoją aplikacją w React Native i (nie)spełniam się literacko walcząc w sieci na klawiatury z ludźmi o innych poglądach. </p>
                    <p>Interesuję się również bezpieczeństwem aplikacji webowych, unikam skrajności, źle znoszę remonty u sąsiadów, nauczyłem się pracować z kotem leżącym na połowie klawiatury, regularnie myślę nad zamieszkaniem w lesie nad Narwią i w wolnych chwilach nie ćwiczę z chodakowską. Półmaratony to max na co mnie dziś stać, dlatego szukam lepszego pracodawcy niż sam sobie jestem.  </p>
                    </div>
                </div>
                </div>
            </div>
            </div>
        </div>
        </section>
        
      </div>
    )
  }
}